import React, { Suspense } from "react";
import LazyComponentLoader from "config/LazyComponentLoader";
import { MedalsList } from "./type";

const SteelComponent = React.lazy(() =>
  LazyComponentLoader(import("./medals&coins/steel/Emblem")),
);
const BronzeComponent = React.lazy(() =>
  LazyComponentLoader(import("./medals&coins/bronze/Emblem")),
);
const SilverComponent = React.lazy(() =>
  LazyComponentLoader(import("./medals&coins/silver/Emblem")),
);
const GoldenComponent = React.lazy(() =>
  LazyComponentLoader(import("./medals&coins/golden/Emblem")),
);
const PlatinumComponent = React.lazy(() =>
  LazyComponentLoader(import("./medals&coins/platinum/Emblem")),
);
const PlatinumEliteComponent = React.lazy(() =>
  LazyComponentLoader(import("./medals&coins/platinum-elite/Emblem")),
);
const DiamondComponent = React.lazy(() =>
  LazyComponentLoader(import("./medals&coins/diamond/Emblem")),
);

export const medalTextComponentsConfig: MedalsList = {
  "0": ({ value }) => (
    <Suspense fallback={null}>
      <SteelComponent value={value} />
    </Suspense>
  ),
  "10": ({ value }) => (
    <Suspense fallback={null}>
      <BronzeComponent value={value} />
    </Suspense>
  ),
  "20": ({ value }) => (
    <Suspense fallback={null}>
      <SilverComponent value={value} />
    </Suspense>
  ),
  "30": ({ value }) => (
    <Suspense fallback={null}>
      <GoldenComponent value={value} />
    </Suspense>
  ),
  "40": ({ value }) => (
    <Suspense fallback={null}>
      <PlatinumComponent value={value} />
    </Suspense>
  ),
  "50": ({ value }) => (
    <Suspense fallback={null}>
      <PlatinumEliteComponent value={value} />
    </Suspense>
  ),
  "60": ({ value }) => (
    <Suspense fallback={null}>
      <DiamondComponent value={value} />
    </Suspense>
  ),
};
