import React from "react";
import { useGetLocale } from "providers";

import { Transition, Typography } from "components";

import { ProfileData } from "..";
import css from "./style.module.scss";

interface StatisticsProps {
  profile?: ProfileData;
}

const Statistics = ({ profile }: StatisticsProps) => {
  const intl = useGetLocale();

  return (
    <div className={css.wrapper}>
      <div className={css.innerWrapper}>
        <div className={css.block}>
          <Typography variant="subtitle1" className={css.label}>
            {intl.formatMessage({
              id: "profile.statistics.netProfit",
            })}
          </Typography>
          <Transition
            animation="fade"
            in={!!profile}
            className={css.valueWrapper}>
            <Typography variant="subtitle2" className={css.value}>
              {`$${profile?.net_profit}`}
            </Typography>
          </Transition>
        </div>

        <div className={css.block}>
          <Typography variant="subtitle1" className={css.label}>
            {intl.formatMessage({
              id: "profile.statistics.totalWagered",
            })}
          </Typography>

          <Transition
            className={css.valueWrapper}
            animation="fade"
            in={!!profile}>
            <Typography variant="subtitle2" className={css.value}>
              {`$${profile?.total_wagered}`}
            </Typography>
          </Transition>
        </div>

        <div className={css.block}>
          <Typography variant="subtitle1" className={css.label}>
            {intl.formatMessage({ id: "profile.statistics.profitAth" })}
          </Typography>
          <Transition
            className={css.valueWrapper}
            animation="fade"
            in={!!profile}>
            <Typography variant="subtitle2" className={css.value}>
              {`$${profile?.profit_ath}`}
            </Typography>
          </Transition>
        </div>

        <div className={css.block}>
          <Typography variant="subtitle1" className={css.label}>
            {intl.formatMessage({ id: "profile.statistics.profitAtl" })}
          </Typography>
          <Transition
            className={css.valueWrapper}
            animation="fade"
            in={!!profile}>
            <Typography variant="subtitle2" className={css.value}>
              {`$${profile?.profit_atl}`}
            </Typography>
          </Transition>
        </div>
        <div className={css.block}>
          <Typography variant="subtitle1" className={css.label}>
            {intl.formatMessage({ id: "profile.statistics.bets" })}
          </Typography>
          <Transition
            className={css.valueWrapper}
            animation="fade"
            in={!!profile}>
            <Typography variant="subtitle2" className={css.value}>
              {profile?.bets}
            </Typography>
          </Transition>
        </div>
        <div className={css.block}>
          <Typography variant="subtitle1" className={css.label}>
            {intl.formatMessage({ id: "profile.statistics.level" })}
          </Typography>
          <Transition
            className={css.valueWrapper}
            animation="fade"
            in={!!profile}>
            <Typography variant="subtitle2" className={css.value}>
              {profile?.level}
            </Typography>
          </Transition>
        </div>
      </div>
    </div>
  );
};

export default Statistics;
