import React, { FunctionComponent, useEffect, useMemo, useState } from "react";
import clsx from "clsx";

import { useGetLocale } from "providers";
import { Skeleton } from "components";

import { ProgressiveModal, Avatar, Typography } from "components";
import { useFetch, useModalLocation } from "hooks";

import { ModalsProps } from "config/routes";
import { useUser } from "providers/User";
import { Rating } from "components";

import Template from "pages/ModalPages/componentsProgressive/Template";
import { medalTextComponentsConfig } from "assets/levels/medalComponents";
import { DialogHeader } from "../../componentsProgressive";
import Statistics from "./Statistics";
import Medals from "./Medals";
import { HiddenProfile } from "./HiddenProfile";
import { StatusAndLikesContent } from "./StatusAndLikesContent";
import { TopFavoriteGamesSection } from "./ProfileInfoSection/TopFavoriteGames";
import { ContestWinHistory } from "./ProfileInfoSection/ContestWinHistory";

import css from "./style.module.scss";

export interface ProfileData {
  bets: string;
  level: string;
  net_profit: string;
  profit_ath: string;
  profit_atl: string;
  total_wagered: string;
  profile_picture_url: string;
  is_hidden: string;
  likes: number;
  status: string | null;
  do_i_like_them: boolean;
}

export interface FetchProfileData {
  success: boolean;
  message: string;

  profile: ProfileData;
}

export interface BetData {
  id: string;
  bet_amt: string;
  coin: string;
  net_profit_inclusive_usd: string;
  payout: string;
  profit: string;
  win: boolean;
}

export type ChartValues = {
  labels: string[];
  data: number[];
};

export interface FetchBetsData {
  success: boolean;
  message: string;

  page: number;
  bets: BetData[];
}

const avatarStyle = {
  width: 92,
  height: 92,
  minHeight: 92,
  minWidth: 92,
};

const games = [
  "CRASH",
  "DICE",
  "PLINKO",
  "LIMBO",
  "VIDEO POKER",
  "KENO",
  "DIAMONDS",
  "ROULETTE",
  "MINES",
  "SLOTS",
];

const Profile: FunctionComponent<ModalsProps> = ({ isOpen }) => {
  const { user } = useUser();
  const intl = useGetLocale();

  const {
    params: { user: name },
    location,
  } = useModalLocation("profile");

  const userName = useMemo<string | undefined>(() => {
    if (name) {
      return name;
    }

    return "";
  }, [location.search, user]);
  const isMe = !userName;

  const [isHidden, setIsHidden] = useState<boolean>(false);

  const [fetchStatisticsProfile, { data: fetchStatisticsProfileData }] =
    useFetch<FetchProfileData>("profile/get");

  const profileAvatar = useMemo(() => {
    const data = fetchStatisticsProfileData;
    if (data && data.profile) {
      return data.profile.profile_picture_url;
    }

    return "";
  }, [fetchStatisticsProfileData]);

  useEffect(() => {
    if (!isOpen) {
      return;
    }

    if (userName === "******") {
      setIsHidden(true);
    } else {
      fetchStatisticsProfile({
        variables: {
          user_name: userName,
        },
      });
    }
  }, [isOpen, fetchStatisticsProfile, userName]);

  const profile = useMemo(() => {
    return fetchStatisticsProfileData?.profile;
  }, [fetchStatisticsProfileData]);

  const ProfileMedal = useMemo(() => {
    if (profile) {
      let levelValue = Math.min(
        Math.floor(Number(profile.level) / 10) * 10,
        60,
      );

      const Component: ({ value }: any) => JSX.Element =
        //@ts-ignore
        medalTextComponentsConfig[levelValue];

      if (Component) {
        return Component;
      }
    }

    return medalTextComponentsConfig["60"];
  }, [profile?.level]);
  const username = userName || user?.user_name || null;
  const renderProfile = () => {
    if (isHidden || profile?.is_hidden === "1") {
      return (
        <>
          <HiddenProfile username={username} />
        </>
      );
    } else if (!profile) {
      return <Skeleton className={css.userSkeleton} height={500} />;
    }
    return (
      <Template classes={{ root: css.rootPadding }}>
        <div className={css.wrapper}>
          <div className={css.userInfoRow}>
            <div className={css.userInfoContainer}>
              <div className={clsx(css.circle, css.userLogo)}>
                <Avatar
                  userName={userName || user?.user_name}
                  style={avatarStyle}
                  avatar={profileAvatar}
                />

                <span className={css.rewardImg}>
                  <ProfileMedal value={profile.level || 0} />
                </span>
              </div>
              <div className={css.userWrapper}>
                <Typography className={css.user}>
                  {userName || user?.user_name ? (
                    userName || user?.user_name
                  ) : (
                    <Skeleton
                      className={css.userSkeleton}
                      width={150}
                      height={28}
                    />
                  )}
                </Typography>
                <div className={css.ratingWrapper}>
                  <Rating
                    value={profile && Number(profile.level)}
                    size="medium"
                  />
                </div>
              </div>
            </div>

            <StatusAndLikesContent
              do_i_like_them={profile.do_i_like_them}
              likes={profile.likes}
              status={profile.status}
              username={username}
              isMe={isMe}
            />
          </div>
          <Medals user={userName || user?.user_name || ""} />
          <Statistics profile={profile} />
          {username && <TopFavoriteGamesSection user={username} />}
          {username && <ContestWinHistory username={username} />}
        </div>
      </Template>
    );
  };

  return (
    <ProgressiveModal
      open={isOpen}
      width={578}
      className={css.modalBackground}
      aria-labelledby="profile-dialog-title"
      aria-describedby="profile-dialog-description">
      <DialogHeader title={intl.formatMessage({ id: "profile.title" })} />

      {renderProfile()}
    </ProgressiveModal>
  );
};

export default Profile;
