import React, { FC, useMemo, useState, MouseEvent, useRef } from "react";

import { OptimizedSlider, SliderValues } from "components";
import { useModalLocation } from "hooks";

import { MedalType } from "pages/ModalPages/pages/Bonuses/components/Medal";
import { ReactComponent as ArrowLeft } from "assets/icons/chevron-left.svg";
import { ReactComponent as ArrowRight } from "assets/icons/chevron-right.svg";

import { MedalsBlockItem } from "./MedalsBlockItem";
import MedalDetails from "./MedalDetails";
import css from "./style.module.scss";
interface IMedalsBlockMedalsBlockProps {
  medals: MedalType[];
  active: MedalType[];
}
export interface MedalDetailsType {
  title: string;
  left: number;
}
export const MedalsBlockMedalsBlock: FC<IMedalsBlockMedalsBlockProps> = ({
  medals,
  active,
}) => {
  const sliderRef = useRef<SliderValues | null>(null);
  const [details, setDetails] = useState<MedalDetailsType | null>(null);
  const medalWidth = 65;
  const increment = () => {
    sliderRef.current?.increment();
  };
  const decrement = () => {
    sliderRef.current?.decrement();
  };
  const { pushHistoryTo } = useModalLocation("bonus");

  const openMedal = (id: string) => {
    pushHistoryTo({
      search: {
        name: "page",
        value: "bonus",
      },
      searchParams: {
        bonus: "medal",
        id,
      },
    });
  };
  const dataHoverCallback = (event: MouseEvent, medal: MedalType) => {
    const rect = event.currentTarget.getBoundingClientRect();
    const width = rect.width / 2;
    const left = rect.left + width;

    setDetails({
      title: medal.medal_name,
      left: left,
    });
  };
  const dataBlurCallback = () => setDetails(null);

  return (
    <div className={css.medalsBlockMedalsBlock}>
      {details && <MedalDetails details={details} />}
      <div className={css.sliderWrapper}>
        <div onClick={decrement} className={css.arrowLeft}>
          <ArrowLeft />
        </div>

        <OptimizedSlider
          cardWidth={medalWidth}
          dataLength={medals.length}
          ref={sliderRef}>
          {medals.map((medal) => {
            const isActiveMedal = active.filter((el) => {
              return el.id === medal.id;
            });
            const style = isActiveMedal.length
              ? { opacity: 1 }
              : { opacity: 0.4 };
            return (
              <MedalsBlockItem
                key={medal.id}
                medal={medal}
                style={style}
                onClick={() => openMedal(medal.id)}
                onMouseOver={(e) => dataHoverCallback(e, medal)}
                onMouseLeave={() => dataBlurCallback()}
              />
            );
          })}
        </OptimizedSlider>
        <div onClick={increment} className={css.arrowRight}>
          <ArrowRight />
        </div>
      </div>
    </div>
  );
};
