import React, { MutableRefObject, useEffect, useRef, useState } from "react";
import { useWindowSize } from "providers";
import { addSmile } from "../../utils";
import { useFetch } from "hooks";

interface IUseContentReturn {
  isOpenEmoji: boolean;
  setIsOpenEmoji: (arg: boolean) => void;
  statusValue: string;
  smileEnter: (value: string) => void;
}
interface IProps {
  status: string | null;
}

export const useContentState = ({ status }: IProps): IUseContentReturn => {
  const [isOpenEmoji, setIsOpenEmoji] = useState<boolean>(false);
  const [statusValue, setStatusValue] = useState<string>(
    status ? `https://cdn.pasino.com/emoji/${status.toLowerCase()}.png` : "",
  );
  const [updateStatus] = useFetch("profile/change-status");

  const smileEnter = (value: string) => {
    const { src, status } = addSmile(value);
    setStatusValue(src);
    updateStatus({
      variables: {
        status: status.toUpperCase(),
      },
    });
  };

  return { isOpenEmoji, setIsOpenEmoji, statusValue, smileEnter };
};
