import clsx from "clsx";
import { Icon } from "components";
import { useFetch } from "hooks";
import React, { FC, useState } from "react";
import { debounce } from "utils/debounce";

import css from "./style.module.scss";

interface ILikes {
  likes: number;
  do_i_like_them: boolean;
  username: string | null;
}
const wait = debounce();
export const Likes: FC<ILikes> = ({ likes, do_i_like_them, username }) => {
  const [isLiked, setIsLiked] = useState(do_i_like_them);
  const [likeCount, setLikeCount] = useState(likes);

  const textWidth = String(likeCount).length * 12;

  const [like] = useFetch("profile/like");
  const [unlike] = useFetch("profile/unlike");

  const onClickHandler = async () => {
    const value = isLiked ? likeCount - 1 : likeCount + 1;
    const fetch = isLiked ? unlike : like;
    setLikeCount(value);
    setIsLiked(!isLiked);

    wait(
      () =>
        fetch({
          variables: {
            user_name: username,
          },
        }),
      300,
    );
  };

  return (
    <div className={css.likes}>
      <Icon
        aria-label="emoticons"
        className={clsx(css.btn, isLiked && css.liked)}
        style={{ padding: "0", borderRadius: "0" }}
        variant={"like"}
        onClick={onClickHandler}
      />
      <span style={{ minWidth: `${textWidth}px` }} className={css.countLike}>
        {likeCount}
      </span>
      Likes
    </div>
  );
};
