import React from "react";
import clsx from "clsx";

import { Avatar, Typography } from "components";
import { useGetLocale } from "providers";

import Template from "pages/ModalPages/componentsProgressive/Template";
import css from "./style.module.scss";
import { TopFavoriteGamesSection } from "../ProfileInfoSection/TopFavoriteGames";

const avatarStyle = {
  width: 70,
  height: 70,
  minHeight: 70,
  minWidth: 70,
};

export const HiddenProfile = ({ username }: { username: string | null }) => {
  const intl = useGetLocale();

  return (
    <Template>
      <div className={css.wrapper}>
        <div className={css.userInfoRow}>
          <div className={clsx(css.circle, css.userLogo)}>
            <Avatar userName="******" style={avatarStyle} avatar={""} />
          </div>
          <div className={css.userWrapper}>
            <Typography className={css.user}>
              {intl.formatMessage({ id: "profile.hiddenUser" })}
            </Typography>
          </div>
        </div>

        <div className={css.block}>
          {intl.formatMessage({ id: "profile.hiddenStatistic" })}
        </div>
      </div>
      <div className={css.favouriteGamesWrapper}>
        {username && <TopFavoriteGamesSection user={username} />}
      </div>
    </Template>
  );
};
