import React, { useEffect, useState, FC } from "react";
import { ReactComponent as ArrowRight } from "assets/icons/profileDitailsArrowRight.svg";
import { useGetLocale } from "providers";

import { useFetch, useModalLocation } from "hooks";

import { MedalFetch, MedalType } from "../../Bonuses/components/Medal";
import { MedalsBlockMedalsBlock } from "./MedalsBlock";
import { MedalsLoader } from "../Loaders/Medals";

import css from "./style.module.scss";
interface UserMedalsFetch {
  medals_farmed: MedalType[];
  message: string;
  success: boolean;
}

interface Props {
  user: string;
}

const Medals: FC<Props> = ({ user }) => {
  const intl = useGetLocale();
  const { pushHistoryTo } = useModalLocation("bonus");

  const [activeMedals, setActiveMedals] = useState<MedalType[]>([]);
  const [allMedals, setAllMedals] = useState<MedalType[]>([]);

  const [allMedalFetch, { data: allMedalsData }] =
    useFetch<MedalFetch>("medal/get-all");
  const [getMedalsFetch, { data: userMedalsData }] =
    useFetch<UserMedalsFetch>("medal/get-medals");

  const getAllMedals = () => allMedalFetch();
  const getUserMedals = (name: string) =>
    getMedalsFetch({
      variables: {
        user_name: name,
      },
    });

  const openMedal = () => {
    pushHistoryTo({
      search: {
        name: "page",
        value: "bonus",
      },
      searchParams: {
        bonus: "medal",
      },
    });
  };

  useEffect(() => {
    getAllMedals();
  }, []);

  useEffect(() => {
    getUserMedals(user);
  }, [user]);

  useEffect(() => {
    if (allMedalsData) {
      setAllMedals(allMedalsData.medals);
    }
  }, [allMedalsData]);

  useEffect(() => {
    if (userMedalsData) {
      setActiveMedals(userMedalsData.medals_farmed);
    }
  }, [userMedalsData]);

  if (!allMedals?.length) {
    return <MedalsLoader />;
  }
  return (
    <div className={css.root}>
      <span className={css.titleWrapper}>
        <h4 className={css.title}>
          {intl.formatMessage({
            id: "bonusPage.modals.medal.title",
          })}
        </h4>
        <p className={css.link} onClick={openMedal}>
          {intl.formatMessage({
            id: "wallet.records.details",
          })}
          <ArrowRight />
        </p>
      </span>
      <MedalsBlockMedalsBlock medals={allMedals} active={activeMedals} />
    </div>
  );
};

export default Medals;
