import React, { FC, useEffect, useState } from "react";
import clsx from "clsx";

import { useGetLocale } from "providers";
import { ProfileInfoSection } from "..";
import { winHistoryColumns } from "./config";
import { useFetch } from "hooks";
import { ContentFooter } from "./components/ContentFooter";

import css from "./style.module.scss";
import { SectionLoader } from "../../Loaders/Section";

interface IProps {
  username: string;
}
export interface IWin {
  date: string;
  position: string;
  prize_amount: string;
  prize_coin: string;
  type: string;
}
export interface IFetch {
  current_page: number;
  total_pages: number;
  wins: IWin[];
}

export const ContestWinHistory: FC<IProps> = ({ username }) => {
  const [winHistory, setWinHistory] = useState<IFetch>();

  const intl = useGetLocale();
  const [fetch, { data }] = useFetch<IFetch>("profile/contest-wins");

  useEffect(() => {
    fetch({
      variables: {
        user_name: username,
        page: 1,
      },
    });
  }, []);
  useEffect(() => {
    if (data) {
      setWinHistory(data);
    }
  }, [data]);

  const winHistoryFetch = (page: number) => {
    fetch({
      variables: {
        user_name: username,
        page,
      },
    });
  };

  if (!winHistory) return <SectionLoader />;
  if (winHistory && winHistory.wins.length === 0) {
    return (
      <ProfileInfoSection
        title={intl.formatMessage({ id: "profile.winHistory.title" })}>
        <div className={css.title}>
          {intl.formatMessage({ id: "profile.winHistory.noWins" })}
        </div>
      </ProfileInfoSection>
    );
  }

  return (
    <ProfileInfoSection
      title={intl.formatMessage({ id: "profile.winHistory.title" })}>
      <div className={css.contentTable}>
        <div className={css.tableBody}>
          {winHistoryColumns.map((column) => {
            const { align, intlIndex } = column;
            return (
              <div className={clsx(css[align], css.cell)}>
                {intl.formatMessage({ id: intlIndex })}
              </div>
            );
          })}
          {winHistoryColumns.map((column) => {
            const { align, dataIndex, title } = column;
            return (
              <div className={css.column}>
                {winHistory.wins.map((win: { [key: string]: any }) => {
                  const { prize_amount, prize_coin } = win;
                  let value = win[dataIndex];
                  const isPrize = title === "Prize";
                  return !isPrize ? (
                    <div className={clsx(css[align], css.cell)}>{value}</div>
                  ) : (
                    <div className={clsx(css[align], css.cell)}>
                      {
                        <div className={css.prize}>
                          <img
                            src={`https://cdn.pasino.com/coins/${prize_coin.toLocaleLowerCase()}.png`}
                            className={css.coinImg}
                          />
                          <p className={css.amount}>{prize_amount}</p>
                        </div>
                      }
                    </div>
                  );
                })}
              </div>
            );
          })}
        </div>
      </div>
      <ContentFooter
        winHistory={winHistory}
        winHistoryFetch={winHistoryFetch}
      />
    </ProfileInfoSection>
  );
};
