import React, { FC, useEffect } from "react";

import { useGetLocale } from "providers";
import { Typography } from "components";
import { ProfileInfoSection } from "..";

import { useFetch } from "hooks";
import { SectionLoader } from "../../Loaders/Section";

import css from "./style.module.scss";
import { favouriteGamesItems } from "./config";
import { Link } from "react-router-dom";

interface IProps {
  user: string;
}
interface IFavouriteFetchList {
  [key: string]: {
    description: string;
    image_url: string | null;
    total_wagered: string;
    type: string;
    name: string;
  };
}

export const TopFavoriteGamesSection: FC<IProps> = ({ user }) => {
  const intl = useGetLocale();
  const [fetch, { data }] = useFetch<{ favourite_games: IFavouriteFetchList }>(
    "profile/favourite-games",
  );
  useEffect(() => {
    fetch({
      variables: {
        user_name: user,
      },
    });
  }, []);
  if (!data) return <SectionLoader />;
  if (data && data && Object.keys(data?.favourite_games).length === 0) {
    return (
      <ProfileInfoSection
        title={intl.formatMessage({ id: "profile.favouriteGames.title" })}>
        <div className={css.title}>
          {intl.formatMessage({ id: "profile.winHistory.noFavouriteGames" })}
        </div>
      </ProfileInfoSection>
    );
  }
  return (
    <ProfileInfoSection
      title={intl.formatMessage({ id: "profile.favouriteGames.title" })}>
      {Object.keys(data?.favourite_games).map((gameKey) => {
        const { description, image_url, total_wagered, name, type } =
          data?.favourite_games[gameKey];
        let img = image_url;
        let goTo = {
          pathname: `/slot/${gameKey}`,
          search: "",
          state: undefined,
        };
        let gameName = name;
        const isSlotGame = type === "SLOT_GAME";
        if (!isSlotGame) {
          const item = favouriteGamesItems.filter(
            (game) => game.dataIndex === gameKey,
          )[0];
          if (item) {
            const { dataIndex, image_url, to } = item;
            img = image_url || "";
            goTo = { pathname: to, search: "", state: undefined };
            gameName = gameKey;
          }
        }
        return (
          <div className={css.itemContent}>
            <div className={css.gameInfo}>
              <Link to={{ ...goTo }}>
                <img src={img || ""} alt="" className={css.img} />
              </Link>

              <div className={css.textInfo}>
                <Link to={{ ...goTo }}>
                  <div className={css.gameName}>{gameName}</div>
                </Link>
                <div className={css.gameDescription}>{description}</div>
              </div>
            </div>
            <div className={css.wageredInfo}>
              <div className={css.wageredInfoTitle}>
                {intl.formatMessage({ id: "profile.favouriteGames.wagered" })}
              </div>
              <div className={css.wageredInfoValue}>{`$ ${total_wagered}`}</div>
            </div>
          </div>
        );
      })}
    </ProfileInfoSection>
  );
};
