import React, { DetailedHTMLProps, FC } from "react";

import { MedalType } from "pages/ModalPages/pages/Bonuses/components/Medal";

import css from "../style.module.scss";
interface IMedalsBlockItem
  extends DetailedHTMLProps<
    React.HTMLAttributes<HTMLDivElement>,
    HTMLDivElement
  > {
  medal: MedalType;
}

export const MedalsBlockItem: FC<IMedalsBlockItem> = ({ medal, ...props }) => {
  const { image_url, medal_name } = medal;
  return (
    <div className={css.item} {...props}>
      <img src={image_url} alt={medal_name} />
    </div>
  );
};
