import React, { FC, useMemo } from "react";
import clsx from "clsx";

import { EmojiWindow, Icon } from "components";

import { useContentState } from "./hooks/useContentState";

import { Likes } from "../Likes";

import css from "./style.module.scss";
interface IProps {
  likes: number;
  do_i_like_them: boolean;
  username: string | null;
  status: string | null;
  isMe: boolean;
}

export const StatusAndLikesContent: FC<IProps> = ({
  likes,
  do_i_like_them,
  username,
  status,
  isMe,
}) => {
  const { isOpenEmoji, setIsOpenEmoji, statusValue, smileEnter } =
    useContentState({ status });

  const onMediaModalClick = () => {
    if (!isMe) return;
    setIsOpenEmoji(!isOpenEmoji);
  };

  const statusIconComponent = useMemo(() => {
    let component = null;
    if (statusValue) {
      component = <img className={css.statusIcon} src={statusValue}></img>;
    } else {
      component = (
        <Icon
          aria-label="emoticons"
          className={css.icon}
          fontSize={25}
          style={{ padding: "0" }}
          variant={"smilingEmoticonFace"}
        />
      );
    }
    return (
      <span
        className={clsx(css.statusBtn, !isMe && css.removeCursor)}
        onClick={onMediaModalClick}>
        {component}
      </span>
    );
  }, [statusValue, isOpenEmoji]);

  return (
    <div className={css.statusAndLikesContent}>
      <div className={css.status}>
        <EmojiWindow
          triangle
          isOpen={isOpenEmoji}
          onClose={() => setIsOpenEmoji(false)}
          smileEnter={smileEnter}
          contentProps={{
            contentWidth: 528,
            contentHeight: 135,
            correctPositionXValue: 67,
            position: "end",
          }}
          releteElement={statusIconComponent}
        />
        <span className={css.text}>My status</span>
      </div>
      <Likes
        do_i_like_them={do_i_like_them}
        likes={likes}
        username={username}
      />
    </div>
  );
};
