import React, { FC, useRef, CSSProperties, useState, useEffect } from "react";
import { MedalDetailsType } from "..";
import css from "./style.module.scss";
interface Props {
  details: MedalDetailsType;
}

const MedalDetails: FC<Props> = ({ details }) => {
  const [rootWidth, setRootWidth] = useState(0);
  const [rootStyle, setRootStyle] = useState<CSSProperties>({});

  const rootRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    setTimeout(() => {
      if (rootRef.current) {
        setRootWidth(rootRef.current.getBoundingClientRect().width);
      }
    }, 0);
  }, [details, rootRef.current]);

  useEffect(() => {
    if (rootRef.current) {
      const parent = rootRef.current.parentElement;
      const left = parent?.getBoundingClientRect().left || 0,
        padding = 12;
      setRootStyle({
        transform: `translateX(${
          details.left - rootWidth / 2 - left + padding
        }px)`,
      });
    }
  }, [rootWidth]);

  const { title } = details;
  return (
    <>
      <div className={css.root} ref={rootRef} style={rootStyle}>
        <p className={css.text}>{title}</p>
        <div className={css.arrow} />
      </div>
    </>
  );
};

export default MedalDetails;
