import React, { FC, useEffect, useMemo, useState } from "react";
import clsx from "clsx";

import { Button } from "components";
import { useGetLocale } from "providers";
import { IFetch } from "../..";
import { ArrowLeft } from "./../../icons/arrowLeftSVG";
import { ArrowRight } from "./../../icons/arrowRightSVG";

import css from "./style.module.scss";
import { debounce } from "utils/debounce";

interface IProps {
  winHistory: IFetch;
  winHistoryFetch: (page: number) => void;
}

const wait = debounce();

export const ContentFooter: FC<IProps> = ({ winHistory, winHistoryFetch }) => {
  const intl = useGetLocale();

  const [page, setPage] = useState(1);

  const { nextDisabled, prevDisabled } = useMemo(() => {
    let prevDisabled = false;
    let nextDisabled = false;
    if (page - 1 === 0) {
      prevDisabled = true;
    }
    if (page + 1 > winHistory.total_pages) {
      nextDisabled = true;
    }
    return { prevDisabled, nextDisabled };
  }, [winHistory.total_pages, page]);

  const onClickHandler = (value: "prev" | "next") => {
    let pageValue = page;
    if (value === "next") {
      pageValue++;
    } else {
      pageValue--;
    }
    setPage(pageValue);
    wait(() => winHistoryFetch(pageValue), 200);
  };

  return (
    <div className={css.contentFooter}>
      <div className={css.arrows}>
        <Button
          className={clsx(css.arrowLeft, css.button)}
          disabled={prevDisabled}
          onClick={() => onClickHandler("prev")}>
          <ArrowLeft fill="white" />
        </Button>
        <Button
          className={clsx(css.arrowRight, css.button)}
          disabled={nextDisabled}
          onClick={() => onClickHandler("next")}>
          <ArrowRight fill="white" />
        </Button>
      </div>
    </div>
  );
};
